    "update adapter information": {                  "en": "update adapter information",                      "de": "Adapterinformationen aktualisieren",              "ru": "Обновить информацию драйвера",                    "pt": "atualizar informações do adaptador",              "nl": "adapter informatie bijwerken",                    "fr": "mettre à jour les informations d'adaptateur",     "it": "aggiorna le informazioni dell'adattatore",        "es": "actualizar la información del adaptador",         "pl": "aktualizuj informacje o adapterze"},
    "update-part1": {                                "en": "Because ioBroker runs on many very different platforms, only manual updating is possible at the moment. To start the manual update, please go to your controller via console and execute the following:", "de": "Aufgrund der Vielzahl an Hardware und Plattformen, auf denen ioBroker läuft, muss der js-controller manuell aktualisiert werden. Dazu auf der Konsole des Hosts folgende Kommandos ausführen:", "ru": "Из-за очень разного оборудования на котором может бежать ioBroker возможно только ручное обновлени. Для ручного обновления нужно открыть командную консоль и ввести следующее:", "pt": "Devido ao grande número de plataformas de hardware que o ioBroker pode ser executado, você precisa atualizar o js-controller manualmente. Para fazer isso, execute os seguintes comandos no console do host:", "nl": "Vanwege het veelvoud aan Platformen waarop ioBroker beschikbaar is kan een systeemupdate alleen handmatig uitgevoerd worden. Ga voor handmatige update naar je systeem via de console en voer het volgende uit:", "fr": "En raison des plates-formes très différentes, où ioBroker peut fonctionner, uniquement la mise à jour manuelle est possible. Pour exécuter la mise à jour manuelle, veuillez exécuter les instructions suivantes via la console de l'hôte:", "it": "Poiché ioBroker funziona su molte piattaforme molto diverse, al momento è possibile solo l'aggiornamento manuale. Per avviare l'aggiornamento manuale, andare al controller tramite console ed eseguire quanto segue:", "es": "Debido al gran número de plataformas de hardware que el ioBroker se puede ejecutar, es necesario actualizar el JS-Controller manualmente. Para ello, ejecute los siguientes comandos en la consola de host:", "pl": "Ponieważ ioBroker działa na wielu bardzo różnych platformach, w tej chwili możliwe jest tylko ręczne aktualizowanie. Aby rozpocząć aktualizację ręczną, przejdź do kontrolera za pomocą konsoli i wykonaj następujące czynności:"},
